.navigation {
    /* width : 70%; */
    /* padding: 1.5%; */
    margin-top: 15%;
    font-size: larger;
    font-family: inherit;
}

.navigation ul {
    /* width : 100%; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type:none !important;
}

.navigation li {
    height: 100%;
}

.navigation li a {
    text-decoration: none;
    color: white;
    display: inline-block;
  position: relative;
  /* width: fit-content; */
  /* color: #0087ca; */
}

.navigation li a:after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 1px;
    bottom: 0;
    left: 0;
    background-color: white;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
    
}

.navigation li a span {
    /* visibility: hidden; */
}

.navigation li a:hover:after {
    cursor: pointer;
    /* text-decoration: underline; */
    transform: scaleX(1);
  transform-origin: bottom left;
}



.navigation li a:hover span {
    /* visibility: visible; */
    /* text-decoration: underline; */
}

.nav-colored { 
    background-color:#0f0c40; 
}

.nav-transparent { 
    background-color:transparent;
}

